@charset "UTF-8";
/*
#3583BF - lite-blue
#e5e5e5 - lite-grey (for dotted border)
#DEF200 - lite-yelow

*/
/* Getting the new tags to behave */
article, aside, audio, canvas, command, datalist, details, embed, figcaption, figure, footer, header, hgroup, keygen, meter, nav, output, progress, section, source, video {
  display: block;
}

mark, rp, rt, ruby, summary, time {
  display: inline;
}

/* Global properties ======================================================== */
html {
  width: 100%;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100%;
  color: #262626;
  min-width: 980px;
  background: #fff;
}

.ic, .ic a {
  border: 0;
  float: right;
  background: #fff;
  color: #f00;
  width: 50%;
  line-height: 10px;
  font-size: 10px;
  margin: -220% 0 0 0;
  overflow: hidden;
  padding: 0;
}

.bg-top-shadow {
  width: 100%;
  background: url(./img/bg-top-shadow.png) center bottom repeat-x;
  padding: 40px 0 52px;
}

#page3 .bg-top-shadow {
  padding: 40px 0 35px;
}

#page4 .bg-top-shadow {
  padding: 40px 0 60px;
}

#page5 .bg-top-shadow {
  padding: 40px 0 62px;
}

.bg-top {
  width: 100%;
  background: url(./img/bg-top-tail.jpg) center top repeat-x;
}

.bg-top-2 {
