  background: url(./img/bg-top-tail2.jpg) center top repeat-y;
}

.bg {
  width: 100%;
  background: url(./img/bg-top.jpg) center top no-repeat;
}

.bg-bot {
  width: 100%;
  background: url(./img/bg-bot-tail.gif) center top repeat-x #fff;
  padding: 30px 0 55px;
}

#page3 .bg-bot, #page4 .bg-bot {
