/*********************************header*************************************/
header {
  width: 100%;
  position: relative;
  z-index: 2;
}

.row-1 {
  width: 100%;
  height: auto;
  border-bottom: 3px rgba(72, 8, 0, 0.5) solid;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#bababa+0,ffffff+51,b5b5b5+100&0.8+0,0.8+100 */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(top, rgba(186, 186, 186, 0.8) 0%, rgba(255, 255, 255, 0.8) 51%, rgba(181, 181, 181, 0.6) 100%);
  background: linear-gradient(to bottom, rgba(186, 186, 186, 0.8) 0%, rgba(255, 255, 255, 0.8) 51%, rgba(181, 181, 181, 0.6) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ccbababa', endColorstr='#ccb5b5b5',GradientType=0 );
  /* IE6-9 */
}

.row-2 {
  width: 100%;
  min-height: 101px;
  background: url(./img/row2-tail.gif) left top repeat-x #fff;
}

.logoBox {
  padding: 15px 0 0 0;
  position: relative;
  font-weight: normal;
  text-transform: uppercase;
  margin-left: -4px;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
}

.logoBox span {
  display: inline-block;
  font-size: 28px;
  line-height: 1.2em;
  color: #949494;
  letter-spacing: -1px;
  padding: 29px 0 0 4px;
}

.logo {
  display: inline-block;
  font-size: 60px;
  line-height: 1.2em;
  color: #262626;
  cursor: pointer;
  letter-spacing: -3px;
}

.logo strong {
  color: #ff9601;
  font-weight: normal;
}

.slogan {
  color: #949494;
  font-style: italic;
  display: table;
  position: relative;
  left: 15px;
  top: -10px;
  text-transform: capitalize;
}

/***** menu *****/
.menu {
  padding: 0;
  width: 100%;
}

.menu li {
  float: left;
  position: relative;
}

/*.menu li:first-child {background:url(./img/menu-a-tail.gif) 0 -71px no-repeat}*/
.menu li a {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  display: block;
  width: 187px;
  font-size: 18px;
  line-height: 1.238em;
  padding: 21px 0 24px 0;
  color: #262626;
  text-align: center;
  text-transform: uppercase;
  border-right: 1px #D0D0D0 solid;
  letter-spacing: -1px;
}

.menu li a:hover {
  color: #FF9601;
}

.menu li a.active {
  color: #fff;
