}

/***** search form *****/
#search-form {
  padding: 35px 0px 0 37px;
}

#search-form fieldset {
  position: relative;
  width: 100%;
  display: block;
  overflow: hidden;
}

#search-form .search-field {
  width: 181px;
  height: 29px;
  background: url(./img/search-input-tail.gif) 0 0 repeat-x;
  border: 1px solid #d7d7d7;
}

#search-form input {
  width: 137px;
  font-size: 13px;
  line-height: 1.23em;
  padding: 6px 10px 7px;
  font-family: Arial, Helvetica, sans-serif;
  background: none;
  border: none;
  margin: 0;
  outline: none;
}

#search-form .search-button {
  display: block;
  width: 17px;
  height: 16px;
  text-indent: -5000px;
  background: url(./img/search-icon.gif) 0 0 no-repeat;
  position: absolute;
  right: 8px;
  top: 7px;
}

/*********************************content*************************************/
#content {
  width: 100%;
  padding: 0;
  position: relative;
  z-index: 1;
}

.gallery {
  padding: 20px 0 26px 20px;
  background: #fff;
  box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.7);
  -webkit-box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.7);
}

.padding {
  padding: 30px 0px 38px;
}

/* -- gallery  begin --*/
div.content {
  /* The display of content is enabled using jQuery so that the slideshow content won't display unless javascript is enabled. */
  display: none;
  width: 620px;
  height: 403px;
  margin: 0;
  padding: 0px;
  float: left;
  overflow: hidden;
}

div.content img {
  position: relative;
  z-index: 2;
}

div.content a, div.navigation a {
  text-decoration: none;
  display: block;
  position: relative;
  cursor: pointer;
}

div.content a img, div.navigation a img {
  position: relative;
  z-index: 1;
}

div.content a span, div.navigation a span {
  display: block;
  width: 140px;
  height: 121px;
  background: #000;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
}

div.content a span.selected,
div.navigation a span.selected {
  display: none !important;
}

div.content a:focus, div.content a:hover, div.content a:active {
  text-decoration: underline;
}

div.controls a {
  padding: 0px;
}

div.ss-controls {
  float: left;
  display: none;
}

div.nav-controls {
  position: absolute;
  left: 0px;
  width: 596px;
  bottom: 0px;
  z-index: 1;
}

div.nav-controls a.prev {
  background: url(./img/prev.gif) no-repeat 0 0;
  width: 39px;
  height: 39px;
  text-indent: -5000px;
  position: absolute;
  bottom: 2px;
  left: 0;
  z-index: 11;
}

div.nav-controls a.prev:hover {
  background: url(./img/prev-h.gif) no-repeat 0 0;
}

div.nav-controls a.next {
  background: url(./img/next.gif) no-repeat 0 0;
  width: 39px;
  height: 39px;
  text-indent: -5000px;
  position: absolute;
  bottom: 2px;
  right: 0;
  z-index: 11;
}

div.nav-controls a.next:hover {
  background: url(./img/next-h.gif) no-repeat 0 0;
}

div.slideshow-container {
  position: relative;
  height: 403px;
  /* This should be set to be at least the height of the largest image in the slideshow */
  z-index: 1;
}

div.loader {
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(images/style/loader.gif);
  background-repeat: no-repeat;
  background-position: center;
  width: 592px;
  height: 470px;
  /* This should be set to be at least the height of the largest image in the slideshow */
}

div.slideshow span.image-wrapper {
  display: block;
  width: 620px;
  height: 403px;
  position: absolute;
  left: 0;
  top: 0;
}

div.slideshow a.advance-link {
  display: block;
  width: 620px;
  height: 403px;
  /* This should be set to be at least the height of the largest image in the slideshow */
  padding: 0;
  margin: 0;
}

div.slideshow a.advance-link img {
  padding: 0;
  margin: 0;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

div.slideshow a.advance-link:hover, div.slideshow a.advance-link:active, div.slideshow a.advance-link:visited {
  text-decoration: none;
}

div.download {
  float: right;
}

div.caption-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 596px;
  height: 151px;
}

span.image-caption {
  display: block;
  position: absolute;
  width: 596px;
  height: 151px;
  top: 0;
  left: 0;
  z-index: 10;
}

div.caption {
  width: 536px;
  height: 128px;
  padding: 23px 30px 0 30px;
  line-height: 18px;
  font-size: 13px;
  color: #fff;
}

div.caption h5 {
  line-height: 18px;
  font-size: 13px;
  color: #fd003f;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
}

div.caption h6 {
  line-height: 18px;
  font-size: 13px;
  color: #fff;
  font-weight: normal;
  margin: 0 0 18px 0;
}

div#thumbs {
  width: 320px;
  height: 403px;
  overflow: hidden;
  padding: 0px 0px 0 0;
}

ul.thumbs {
  float: right;
  margin-left: 0;
  padding: 0;
  width: 320px;
  height: auto;
  overflow: visible;
}

ul.thumbs li {
  height: 121px;
  float: left;
  margin: 0 20px 20px 0;
  overflow: hidden;
  padding: 0;
  vertical-align: top;
}

ul.thumbs li img {
  border: none;
}

a.thumb:focus {
  outline: none;
}

div.pagination {
  clear: both;
  text-align: center;
  position: relative;
  z-index: 10;
}

div.top.pagination {
  display: none;
}

div.navigation div.bottom {
  display: none;
}

div.pagination a, div.pagination span.current, div.pagination span.ellipsis {
  padding: 0 4px;
  font-weight: bold;
  color: #fff;
}

div.pagination a:hover {
  text-decoration: none;
  color: #ffeaa8;
}

div.pagination span.current {
  color: #ffeaa8;
}

div.pagination span.ellipsis {
  border: none;
  padding: 5px 0 3px 2px;
}

