}

#contact-form input {
  float: left;
  width: 406px;
  font-size: 13px;
  line-height: 1.23em;
  color: #262626;
  padding: 6px 10px;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  border: 1px solid #d9d9d9;
  background: #fcfcfc;
  outline: none;
}

#contact-form textarea {
  float: left;
  height: 192px;
  overflow: auto;
  width: 776px;
  font-size: 13px;
  line-height: 1.23em;
  color: #404040;
  padding: 6px 10px;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  border: 1px solid #d9d9d9;
  background: #fcfcfc;
  outline: none;
}

.text-form {
  float: left;
  display: block;
  font-size: 13px;
  line-height: 1.23em;
  width: 81px;
  color: #262626;
  font-family: Arial, Helvetica, sans-serif;
  padding-top: 3px;
}

.buttons {
  padding: 18px 30px 0 0;
  text-align: right;
}

.buttons a {
  margin-left: 10px;
  padding: 10px 42px 11px;
}

/****************************footer************************/
footer {
  width: 100%;
  padding: 40px 0 33px;
  background: url(./img/footer-tail.gif) center top repeat-x #fff;
}

.phone-numb {
  display: inline-block;
  font-size: 42px;
  line-height: 1.2em;
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  letter-spacing: -1px;
  margin-top: -4px;
}

.phone-numb span {
  display: inline-block;
  font-size: 28px;
  color: #949494;
  padding-top: 3px;
}

.list-services {
  padding: 0;
}

.list-services li {
  float: left;
  padding: 0 0 0 20px;
}

.list-services li:first-child {
  padding: 0;
}

.list-services li a {
  display: block;
  width: 60px;
  height: 45px;
  background: url(./img/social-icons.png) left top no-repeat;
}

.list-services li a.item-2 {
  background-position: -80px 0;
}

.list-services li a.item-3 {
  background-position: -160px 0;
}

.list-services li a.item-4 {
  background-position: -240px 0;
}

.sep {
  background: transparent url("./img/separator-light.png") repeat-x scroll left center;
  padding: 10px 0;
  margin: 20px 0;
}

/*sidebar*/
.lastPhoto > ul > li {
  border-top: 2px #E5E5E5 solid;
  padding: 6px 0;
}

.lastPhoto > ul > li:first-child {
  border-top: none;
  padding: 0 0 6px 0;
}

.lastPhoto-title {
  margin: 0 0 6px 0;
}

.lastPhoto-title a {
  text-decoration: none;
  color: #400001;
  font-weight: bold;
}

.lastPhoto-title a:hover {
  color: #FF9601;
}

.lastPhoto-img {
  display: table;
  border: 1px solid #bfbfbf;
  float: left;
  margin: 0 6px 4px 0;
  box-shadow: 0 3px 7px 0 rgba(50, 50, 50, 0.31);
}

.lastPhoto-img img {
  display: block;
  width: 60px;
  height: 60px;
  border: 3px #fff solid;
  background: graytext;
}

.lastPhoto-post {
  font-size: 12px;
}

.lastPhoto-cnt i {
  position: relative;
  top: 2px;
  padding-left: 10px;
  color: #949494;
}

.lastPhoto-cnt i:first-child {
  padding-left: 0;
}

.lastPhoto-cnt strong {
  color: #6d6d6d;
}

/*последние или популярные материалы в сайдбаре*/
.lastArticle > ul > li {
  border-top: 2px #E5E5E5 solid;
  padding: 20px 0;
}

.lastArticle > ul > li:first-child {
  border-top: none;
  padding: 0 0 20px 0;
}

a.lastArticle-title {
  display: block;
}

.lastArticle-cnt i {
  position: relative;
  top: 3px;
}

.lastArticle-img {
  display: table;
  float: left;
  margin: 4px 6px 4px 0;
}

.lastArticle-img img {
  display: block;
  width: 50px;
  height: 50px;
  background: graytext;
  border-radius: 25px;
}

.lastArticle-post {
  margin-bottom: 0;
}

/*вертикальный блок материалов*/
.vertBlock {
  margin-top: 30px;
}

.vertBlock > article {
  margin-bottom: 30px;
}

/*хлебные крошки*/
ul#breadcrumbs-one {
  margin: 0 0 10px 0;
  padding: 0;
  list-style: none;
}

#breadcrumbs-one {
  background: #eee;
  border-width: 1px;
  border-style: solid;
  border-color: #f5f5f5 #e5e5e5 #ccc;
  border-radius: 5px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  width: 100%;
}

#breadcrumbs-one li {
  float: left;
}

#breadcrumbs-one a, #breadcrumbs-one span {
  padding: 0.4em 0em 0.4em 1.1em;
  float: left;
  text-decoration: none;
  color: #444;
  position: relative;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  background-color: #ddd;
  background-image: -webkit-linear-gradient(left, #f5f5f5, #ddd);
  background-image: linear-gradient(to right, #f5f5f5, #ddd);
  font-size: 12px;
  margin-right: 2.2px;
}

#breadcrumbs-one li:first-child a {
  padding-left: 1em;
  border-radius: 5px 0 0 5px;
}

#breadcrumbs-one a:hover {
  background: #fff;
}

#breadcrumbs-one a::after, #breadcrumbs-one span::after,
#breadcrumbs-one a::before, #breadcrumbs-one span::before {
  content: "";
  position: absolute;
  top: 50%;
  margin-top: -1.5em;
  border-top: 1.5em solid transparent;
  border-bottom: 1.5em solid transparent;
  border-left: 1em solid;
  right: -1em;
}

#breadcrumbs-one a::after, #breadcrumbs-one span::after {
  z-index: 2;
  border-left-color: #ddd;
}

#breadcrumbs-one a::before, #breadcrumbs-one span::before {
  border-left-color: #ccc;
  right: -1.1em;
  z-index: 1;
}

#breadcrumbs-one a:hover::after {
  border-left-color: #fff;
}

#breadcrumbs-one .current,
#breadcrumbs-one .current:hover {
  font-weight: bold;
  background: none;
}

#breadcrumbs-one .current::after,
#breadcrumbs-one .current::before {
  content: normal;
}

/*пагинация*/
.pagination-box {
  display: table;
  overflow: hidden;
  margin: 20px auto;
}

.pagination-box ul > li {
  float: left;
  display: block;
}

.pagination-box ul > li a {
  padding: 8px 16px;
  display: inline-block;
  text-decoration: none;
  font-weight: bold;
}

.pagination-box ul > li a:hover {
  background-color: #CCCCCC;
  color: #000;
}

.pagination-box ul > li.active a {
  background-color: #F44336;
}

.pagination-box ul > li.active a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

.disabled {
  display: none !important;
}

/*Photo style*/
.img-radius-100 {
  display: block;
  width: 100px;
  height: 100px;
  background: graytext;
  border-radius: 56px;
  border: 6px #e5e5e5 solid;
  margin: 0 auto;
  padding: 1px;
}

.box-shedow-pics {
  box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 3px 5px 8px rgba(0, 0, 0, 0.5);
}

.a-block {
  position: relative;
  display: table;
}

.bigger {
  position: absolute;
  bottom: 0;
  left: 0;
}

.bigger i {
  color: #fff;
  background-color: #3583BF;
  padding: 10px;
}

.galery-title {
  border-bottom: 1px #e5e5e5 dotted;
}

.galery-title small {
  color: #949494;
  display: inline-block;
  text-align: center;
}

.galery-title a i {
  margin: 3px 6px 0 0;
}

/*Tags styling*/
.tags {
  overflow: hidden;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px #e5e5e5 dotted;
}

.tags li {
  float: left;
  display: table;
  margin-right: 12px;
}

.tags a {
  display: inline-block;
  height: 25px;
  background: url("./img/tag.png") no-repeat;
  font: bold 11px/22px Arial, sans-serif;
  color: #963;
  text-decoration: none;
  padding: 0 0px 0 20px;
  text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.4);
  white-space: nowrap;
  position: relative;
}

.tags a:after {
  content: '';
  position: absolute;
  top: 0;
  right: -8px;
  width: 8px;
  height: 25px;
  background: url("./img/tag.png") 100% 0 no-repeat;
  background-position: right top;
}

.tags a:hover {
  color: #FFF;
  text-shadow: -1px -1px 0 rgba(153, 102, 51, 0.3);
}

